  <span class="token attr-name">:columns</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>[{title: <span class="token punctuation">'</span>ID<span class="token punctuation">'</span>, dataIndex: <span class="token punctuation">'</span>id<span class="token punctuation">'</span>}, {title: <span class="token punctuation">'</span>商品名<span class="token punctuation">'</span>, dataIndex: <span class="token punctuation">'</span>goods_name<span class="token punctuation">'</span>}, {title: <span class="token punctuation">'</span>价格<span class="token punctuation">'</span>, dataIndex: <span class="token punctuation">'</span>price<span class="token punctuation">'</span>}, {title: <span class="token punctuation">'</span>生产日期<span class="token punctuation">'</span>, dataIndex: <span class="token punctuation">'</span>ctime<span class="token punctuation">'</span>}]<span class="token punctuation">"</span></span>
  <span class="token attr-name">:data-source</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>[{id: <span class="token punctuation">'</span>1<span class="token punctuation">'</span>, goods_name: <span class="token punctuation">'</span>sk2<span class="token punctuation">'</span>, price: <span class="token punctuation">'</span>123<span class="token punctuation">'</span>, ctime: <span class="token punctuation">'</span>2021-09-26<span class="token punctuation">'</span>},{id: <span class="token punctuation">'</span>1<span class="token punctuation">'</span>, goods_name: <span class="token punctuation">'</span>sk2<span class="token punctuation">'</span>, price: <span class="token punctuation">'</span>123<span class="token punctuation">'</span>, ctime: <span class="token punctuation">'</span>2021-09-26<span class="token punctuation">'</span>},{id: <span class="token punctuation">'</span>1<span class="token punctuation">'</span>, goods_name: <span class="token punctuation">'</span>sk2<span class="token punctuation">'</span>, price: <span class="token punctuation">'</span>123<span class="token punctuation">'</span>, ctime: <span class="token punctuation">'</span>2021-09-26<span class="token punctuation">'</span>},{id: <span class="token punctuation">'</span>1<span class="token punctuation">'</span>, goods_name: <span class="token punctuation">'</span>sk2<span class="token punctuation">'</span>, price: <span class="token punctuation">'</span>123<span class="token punctuation">'</span>, ctime: <span class="token punctuation">'</span>2021-09-26<span class="token punctuation">'</span>},{id: <span class="token punctuation">'</span>1<span class="token punctuation">'</span>, goods_name: <span class="token punctuation">'</span>sk2<span class="token punctuation">'</span>, price: <span class="token punctuation">'</span>123<span class="token punctuation">'</span>, ctime: <span class="token punctuation">'</span>2021-09-26<span class="token punctuation">'</span>}]<span class="token punctuation">"</span></span>
<span class="token punctuation">></span></span><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>wd-table</span><span class="token punctuation">></span></span>
